body {
    margin: 8% 28%;
    border: 1px solid #ececec;
    border-radius: 5px;
    background-color: #d6d6d6;
}
* {
	margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
}

.main {
    padding: 0px 20px 10px 20px;
    background-color: white;
}

.header, .user, .number {
    display: flex;
    flex-direction: row;
}

.user > img {
    height: 70px;
    width: 70px;
    border-radius: 50px;
}

.social, .contacts {
    display: flex;
    flex-direction: column;
}

.crm {
    background-color: #ececec;
}

.header > h1 {
    width: 34%;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 26px;
    border-radius: 1px;
}

.header {
    padding-bottom: 10px;
    background-color: white;
}

.user {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0px 0 20px 0;
}

.user img {
    margin-right: 15px;
}

.name {
    font-weight: 400;
    font-size: 20px;
}

h2 {
    margin-bottom: 6px;
    font-weight: 600;
    font-size: 18px;
}

.social, .contacts, .adds {
    margin-bottom: 10px;
    width: auto;
}

.social .item {
    margin: 3px 0px;
    color: #928a8a;
}

.contacts div, .adds {
    margin-bottom: 3px;
    color: #928a8a;
}

.item:hover, .contacts .number:hover, .number:hover div {
    color: #2196f3;
    cursor: pointer;
    -webkit-transition: all 0.1s ease;
    transition: all 0.1s ease;
}

.client {
    color: #2196f3;
}

.item i {
    width: 25px;
    font-size: 18px;
}

.number div {
    width: 100px;
}

.number i {
    margin-right: 6px;
    font-size: 12px;
}

.number {
    display: flex;
    flex-direction: row;
    align-items: baseline;
}

.adds p {
    text-align: justify;
}

.react-contextmenu-item {
    color: black;
    background: white;
    font-size: 200;
    font-family: 'Roboto', sans-serif;
    padding: 5px 10px;
}

.react-contextmenu-item:hover {
    background: #ececec;
}

.react-contextmenu-item:hover {
    cursor: pointer;
}

.react-contextmenu {
    border: 1px solid #ececec;
    border-radius: 5px;
    background: white;
}
